<template><section><html><head></head><body><h1>双圆涟漪</h1>
<p><code v-pre="">vc-ripple-circle-double</code> 组件用于加载双圆涟漪效果，实质是通过 <code v-pre="">vc-entity</code> 和 <code v-pre="">vc-graphics-ellipse</code> 按一定时间间隔加载 2 个圆，然后不停循环修改圆的半径及材质透明度实现。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>加载双圆涟漪效果</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-ripple-circle-double @ready="subReady" image-url="./statics/SampleData/images/redCircle2.png" :position="position" ref="circle" v-if="flag"></vc-ripple-circle-double>
        <vc-ripple-circle-double @ready="subReady" image-url="./statics/SampleData/images/redCircle2.png" :position="position" ref="circle" v-if="flag" :height="3000"></vc-ripple-circle-double>
        <vc-entity>
          <vc-graphics-polyline :positions="positions" :width="5" :material="material"></vc-graphics-polyline>
        </vc-entity>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
