      <span class="hljs-tag">&lt;<span class="hljs-name">vc-ripple-circle-double</span>
        <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
        <span class="hljs-attr">image-url</span>=<span class="hljs-string">"./statics/SampleData/images/redCircle2.png"</span>
        <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"circle"</span>
        <span class="hljs-attr">v-if</span>=<span class="hljs-string">"flag"</span>
        <span class="hljs-attr">:height</span>=<span class="hljs-string">"3000"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-ripple-circle-double</span>&gt;</span>
