      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span> },
        <span class="hljs-attr">positions</span>: [{ <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">0</span> }, { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">3000</span> }],
