        <span class="hljs-comment">// vm.$refs.circle.unload()</span>
        <span class="hljs-comment">// vm.$refs.circle.load()</span>
        <span class="hljs-comment">// vm.$refs.circle.reload()</span>
        <span class="hljs-variable language_">window</span>.<span class="hljs-property">vm</span> = <span class="hljs-variable language_">this</span>
      },
      <span class="hljs-title function_">subReady</span>(<span class="hljs-params"></span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">depthTestAgainstTerrain</span> = <span class="hljs-literal">true</span>
        viewer.<span class="hljs-title function_">zoomTo</span>(viewer.<span class="hljs-property">entities</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定双圆涟漪效果实体添加的位置。结构：{ lng: number, lat: number, height: number } 或者 Cesium.Cartesian3</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定双圆涟漪效果是否可见。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">undefined</code></td>
<td><code v-pre="">optional</code> 指定双圆涟漪效果椭圆高度。单位米。</td>
</tr>
<tr>
<td>minRadius</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定双圆涟漪效果最小半径。单位米。</td>
</tr>
<tr>
<td>maxRadius</td>
<td>Number</td>
<td><code v-pre="">3000</code></td>
<td><code v-pre="">optional</code> 指定双圆涟漪效果最大半径。单位米。</td>
</tr>
<tr>
<td>deviationRadius</td>
<td>Number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code> 双圆半径改变大小的差值，值越大速度越快。</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre="">3000</code></td>
<td><code v-pre="">optional</code> 两个圆的时间间隔，单位毫秒。</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre="">''</code></td>
<td><code v-pre="">optional</code> 指定用于表达双圆涟漪效果的图片。</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> 指定双圆涟漪效果效果颜色。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及实体数组。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 },
          positions: [{ lng: 117.217124, lat: 31.809777, height: 0 }, { lng: 117.217124, lat: 31.809777, height: 3000 }],
          material: {
            fabric: {
              type: 'PolylineGlow',
              uniforms: { color: 'red', glowPower: 0.5 }
            }
          },
          flag: true
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          window.vm = this
        },
        subReady() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.zoomTo(viewer.entities)
        }
      }
    }
  </script>